<template>
  <NavBar>
    <template #left>
      <div class="back" @click="backClick">
        <img src="~assets/img/common/back.svg" alt="">
      </div>
    </template>
    <template #center>
      <div class="title">
        <div v-for="(item, index) in titles"  :key="index"
          class="title-item" :class="{active: currentIndex === index}"
          @click="titleClick(index)">{{item}}
        </div>
      </div>
    </template>
  </NavBar>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'

export default {
  name: "DetailNavBar",
  components:{
    NavBar,
  },
  data() {
    return {
      titles: ['商品', '参数', '评论', '推荐'],
      currentIndex: 0
    }
  },
  methods: {
    titleClick(index) {
      this.currentIndex = index
      this.$emit('titleClick', index)
    },
    backClick() {
      this.$router.back()
    }
  }
}
</script>

<style scoped>
.title {
  display: flex;
  align-items: center;
  font-size: 12px;
}
.title-item {
  flex: 1;
}
.active {
  color: var(--color-high-text);
}
.back{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>